<template>
	<div class="payment_status">
		<div class="status_top">
			<van-icon :name="statusIcon" :class="statusClass" />
			<div>{{statusText}}</div>
		</div>

		<div class="time_down status_text" v-if="isSuccess"><span ref="timeSpan" class="red">3秒</span>后返回到任务列表</div>
		<div class="status_text" v-else>系统繁忙, 资料提交遇到问题, 请您稍后再试!</div>

		<div class="status_goLink">
			<router-link class="red" :to="{name: details, params: {id: taskId, status: 20}}">查看任务详情<van-icon name="arrow" /></router-link>
		</div>
	</div>
</template>

<script>
	export default {
		name: "task-status",
		props: {
			status: String,
      taskId: String,
      redirect: String,
      details: String
		},

		data() {
			return {
				isSuccess: true,
        taskId : "0",
        timer : null
			}
		},

		computed: {
			statusText() {
				return this.isSuccess ? '提交成功' : '提交失败'
			},
			statusIcon() {
				return this.isSuccess ? 'checked' : 'fail'
			},
			statusClass() {
				return this.isSuccess ? 'success_icon' : 'fail_icon'
			}
		},

		activated() {
			this.isSuccess = this.status === "success";
		},

    created(){
		  let second = 3;
		  this.timer = setInterval(() => {
        second--;
        if(second <= 0){
          this.$router.replace({name: this.redirect})
          return false;
        }
		    this.$refs.timeSpan.innerHTML = second + "秒";
      }, 1000);
    },

    beforeDestroy(){
		  if(this.timer){
		    clearInterval(this.timer);
      }
    }
	}

</script>


<style lang="scss" scoped>


	.payment_status {
		padding-top: 30px;
		box-sizing: border-box;
		background-color: #fff;
		text-align: center;
	}

	.status_top{
		margin-bottom: 15px;
		i{
			margin-bottom: 5px;
		}
		>div{
			font-size: 18px;
		}
	}

	.status_text{
		color: $font-color-gray;
		margin-bottom: 50px;
	}

	.status_icon {
		font-size: 80px;
	}

	i.success_icon {
		@extend .status_icon;
		color: #06bf04
	}

	i.fail_icon {
		@extend .status_icon;
		color: #f44
	}

</style>



